"use client";

import React from "react";
import BookInfo from "./ui/Book";
import SimilarBooks from "./ui/SimilarBooks";
import useBook from "@/hooks/data/books/useBook";
import useWriters from "@/hooks/data/books/writers/useWriters";
import { Tables } from "@/types/database.types";
import { BookProvider } from "./provider/BookProvider";
import { Spinner } from "@/app/ui/Spinner";
export default function Page({
  params: {
    book
  }
}: {
  params: {
    book: string;
  };
}) {
  const bookData = useBook(book);
  if (bookData.isLoading) return <div className="flex h-full min-h-[40rem] w-full items-center justify-center bg-transparent bg-opacity-25">
        <Spinner />
      </div>;
  return <BookProvider book={bookData.data?.data ?? null} data-sentry-element="BookProvider" data-sentry-component="Page" data-sentry-source-file="page.tsx">
      <div className="h-full w-full">
        <BookInfo data-sentry-element="BookInfo" data-sentry-source-file="page.tsx" />
        <hr className="my-5 border border-black" />
        <SimilarBooks books={bookData.data?.data?.recommended_books ?? null} data-sentry-element="SimilarBooks" data-sentry-source-file="page.tsx" />
      </div>
    </BookProvider>;
}